import React, {Component} from 'react'
import {HashLink} from 'react-router-hash-link'
import DarkModeButton from './DarkModeButton'

export default class PreloaderRemover extends Component {
    componentDidMount() {
        let navbar = document.getElementById('Header') as HTMLElement;
        let toggleBackground = document.getElementById('ToggleBackground') as HTMLElement;
        let toggleButton = document.getElementById('ToggleButton') as HTMLElement;
        let sticky: number = navbar.offsetTop + 60;

        window.onscroll = function () {
            navbarFixing()
        }

        function navbarFixing() {
            if (window.pageYOffset >= sticky) {
                navbar.classList.add('HeaderFixed')
                toggleButton.classList.add('ToggleButtonFixed')
                toggleBackground.classList.add('ToggleBackgroundFixed')
            } else {
                navbar.classList.remove('HeaderFixed')
                toggleButton.classList.remove('ToggleButtonFixed')
                toggleBackground.classList.remove('ToggleBackgroundFixed')
            }
        }
    }

    render() {
        return (
            <header id={'Header'}>
                <div className={'HeaderButton'}>
                    <HashLink to={'/#About'}>–û–±–æ –º–Ω–µ</HashLink>
                </div>
                <div className={'HeaderButton'}>
                    <HashLink to={'/#Works'}>–ü—Ä–æ–µ–∫—Ç—ã</HashLink>
                </div>
                <div className={'HeaderButton'}>
                    <HashLink to={'/#Contacts'}>–ö–æ–Ω—Ç–∞–∫—Ç—ã</HashLink>
                </div>
                <div className={'HeaderButton'}>
                    <a href={'https://github.com/cat-in-a-box'} target={'blank'}>GitHub</a>
                </div>
                <DarkModeButton/>
            </header>
        )
    }
}
