import React from 'react';

function RestartButton(event:any) {
	//–õ–∏—Å—Ç–µ–Ω–µ—Ä —Å–∏–¥–∏—Ç –∏ –∂–º–µ—Ç –ø–æ–∫–∞ —Ç—ã –Ω–∞–∂–º–µ—à—å –Ω–∞ –∫–Ω–æ–ø–∫—É
	document.addEventListener("keydown", RestartButton);

	//–ù–∞–∑–Ω–∞—á–µ–Ω–∏–µ –∫–æ–¥–∞ –Ω–∞ –ø—Ä–æ–±–µ–ª
	let spaceBar: number = 32;
	let spaceBarPressed:any = event.keyCode;

	//–ü–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∫–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
	if (spaceBarPressed === spaceBar) {
		window.location.reload();
	}

    return (
        <a className={"SnakeRestartButton"} href={"https://cat-in-a-box.github.io/snake"}>–ü–µ—Ä–µ–∑–∞–ø—É—Å–∫ (–ü—Ä–æ–±–µ–ª)</a>
    )
}

export default RestartButton;

