import React from 'react';

export default class GameProcess extends React.Component {

    componentDidMount() {
        //—Å–∫–æ—Ä–æ—Å—Ç—å –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∏–≥—Ä—ã
        const GameRefreshSpeed:number = 120;
        //—Ü–≤–µ—Ç –±–æ—Ä–¥–µ—Ä–∞ –∫–∞–Ω–≤–∞—Å–∞
        const CanvasBorderColor:string = 'black';
        //—Ü–≤–µ—Ç –±–µ–∫–≥—Ä–∞—É–Ω–¥–∞ –∫–∞–Ω–≤–∞—Å–∞
        let CanvasBackgroundColor:string = "white";
        //—Ü–≤–µ—Ç —Ç–µ–ª–∞ –∑–º–µ–∏
        const SnakeColor:string = 'rgba(145,255,133,0.7)';
        //—Ü–≤–µ—Ç –±–æ—Ä–¥–µ—Ä–∞ –∑–º–µ–∏
        const SnakeBorderColor:string = 'black';
        //—Ü–≤–µ—Ç –µ–¥—ã
        const FoodColor:string = '#ffa08f';
        //—Ü–≤–µ—Ç –±–æ—Ä–¥–µ—Ä–∞ –µ–¥—ã
        const FoodBorderColor:string = 'black';

        //–∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã —Å–ø–∞—É–Ω–∞ –∑–º–µ–π–∫–∏
        let snake = [
            {x: 150, y: 150},
            {x: 120, y: 150},
            {x: 90, y: 150},
            {x: 60, y: 150},
            {x: 30, y: 150}
        ];

        // –°—á–µ—Ç –∏–≥—Ä–æ–∫–∞
        let score:any = 0!;
        // –ú–µ–Ω—è–µ—Ç—Å—è –Ω–∞ true –∫–æ–≥–¥–∞ –∑–º–µ–π–∫–∞ –º–µ–Ω—è–µ—Ç –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ
        let changingDirection:boolean = false;
        // x-–∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞ –µ–¥—ã
        let foodX:number;
        // y-–∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞ –µ–¥—ã
        let foodY:number;
        // –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–∞—è —Å–∫–æ—Ä–æ—Å—Ç—å –¥–≤–∏–∂–µ–Ω–∏—è
        let dx:number = 30;
        // –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–∞—è —Å–∫–æ—Ä–æ—Å—Ç—å –¥–≤–∏–∂–µ–Ω–∏—è
        let dy:number = 0;

        const gameCanvas = document.getElementById("SnakeGameCanvas") as HTMLCanvasElement;
        const ctx = gameCanvas.getContext("2d")!;

        // –°—Ç–∞—Ä—Ç –∏–≥—Ä—ã
        main();
        // –°–æ–∑–¥–∞—Ç—å –µ–¥—É
        spawnFood();
        // –í—ã–∑—ã–≤–∞–µ—Ç changeDirection –∫–æ–≥–¥–∞ –Ω–∞–∂–∏–º–∞–µ—à—å –∫–ª–∞–≤–∏—à—É
        document.addEventListener("keydown", changeDirection);

        /**
         * –ì–ª–∞–≤–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è
         * –ü–æ—Å—Ç–æ—è–Ω–Ω–æ –≤—ã–ø–æ–ª–Ω—è–µ—Ç—Å—è, —á—Ç–æ–±—ã –∏–≥—Ä–∞ –∏–≥—Ä–∞–ª–∞—Å—å
         **/

        let victoryText = document.getElementById("SnakeScoreUnderText") as HTMLElement;

        function main() {
            if (GameEnd()) {
                victoryText.innerHTML = "–ù–µ –æ—Ç—á–∞–∏–≤–∞–π—Ç–µ—Å—å, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â–µ —Ä–∞–∑! :)";
                return;
            }
            if (localStorage.getItem('darkModeStatus') === 'true') {
                CanvasBackgroundColor = "rgba(72, 72, 84, 1)";
            }
            else {
                CanvasBackgroundColor = "white";
            }
            // –ó–∞–∫–∞–Ω—á–∏–≤–∞–µ—Ç –∏–≥—Ä—É, –µ—Å–ª–∏ —Å–æ–±–ª—é–¥–µ–Ω—ã –≤—Å–µ —É—Å–ª–æ–≤–∏—è
            setTimeout(function onTick() {
                victoryText.innerHTML = "–î–≤–∏–≥–∞–π –∑–º–µ–π–∫—É —Å –ø–æ–º–æ—â—å—é —Å—Ç—Ä–µ–ª–æ–∫ –Ω–∞ –∫–ª–∞–≤–∏–∞—Ç—É—Ä–µ";
                changingDirection = false;
                prepareCanvas();
                drawFood();
                moveSnake();
                drawSnake();
                main();
            }, GameRefreshSpeed)
        }


        /**
         * –ò–∑–º–µ–Ω—è–µ—Ç —Ü–≤–µ—Ç –±–µ–∫–≥—Ä–∞—É–Ω–¥–∞ –Ω–∞ CanvasBackgroundColor –∏
         * —Ä–∏—Å—É–µ—Ç –≤–æ–∫—Ä—É–≥ –Ω–µ–≥–æ –±–æ—Ä–¥–µ—Ä
         **/
        function prepareCanvas() {
            ctx.fillStyle = CanvasBackgroundColor;
            ctx.strokeStyle = CanvasBorderColor;
            ctx.fillRect(0, 0, gameCanvas.width, gameCanvas.height);
            ctx.strokeRect(0, 0, gameCanvas.width, gameCanvas.height);
        }


        /**
         * –†–∏—Å—É–µ—Ç –µ–¥—É
         **/
        function drawFood() {
            ctx.fillStyle = FoodColor;
            ctx.strokeStyle = FoodBorderColor;
            ctx.fillRect(foodX, foodY, 30, 30);
            ctx.strokeRect(foodX, foodY, 30, 30);
        }


        /**
         * –ü—Ä–æ–¥–≤–∏–≥–∞–µ—Ç –∑–º–µ–π–∫—É –≤–ø–µ—Ä–µ–¥ –ø–æ —Ö-–∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞–º –∏ y-–∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞–º
         **/
        function moveSnake() {
            // –°–æ–∑–¥–∞–µ—Ç –Ω–æ–≤—É—é –≥–æ–ª–æ–≤—É —É –∑–º–µ–π–∫–∏
            const newHead = {x: snake[0].x + dx, y: snake[0].y + dy};
            // –î–æ–±–∞–≤–ª—è–µ—Ç –Ω–æ–≤—É—é –≥–æ–ª–æ–≤—É –∫ –Ω–∞—á–∞–ª—É —Ç–µ–ª–∞ –∑–º–µ–π–∫–∏
            snake.unshift(newHead);
            const FoodEaten = snake[0].x === foodX && snake[0].y === foodY;
            if (FoodEaten) {
                // –ü–æ–≤—ã—à–∞–µ—Ç —Å—á—ë—Ç
                score += 1;
                // –û—Ç–æ–±—Ä–∞–∂–∞–µ—Ç —Å—á–µ—Ç –Ω–∞ —ç–∫—Ä–∞–Ω–µ
                let displayScore:any = document.getElementById('SnakeScore')!;
                displayScore.innerHTML = score;
                // –°–æ–∑–¥–∞–µ—Ç –Ω–æ–≤—É—é –µ–¥—É
                spawnFood();
            } else {
                // –£–±–∏—Ä–∞–µ—Ç –ø–æ—Å–ª–µ–¥–Ω—é—é —á–∞—Å—Ç—å —Ç–µ–ª–∞ –∑–º–µ–π–∫–∏
                snake.pop();
            }
        }

        /**
         * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç true –µ—Å–ª–∏ –≥–æ–ª–æ–≤–∞ –∑–º–µ–π–∫–∏ –∫–æ—Å–Ω—É–ª–∞—Å—å —Å—Ç–µ–Ω—ã –∏–ª–∏ —Å–∞–º–æ–π —Å–µ–±—è
         **/
        function GameEnd() {
            for (let i = 4; i < snake.length; i++) {
                if (snake[i].x === snake[0].x && snake[i].y === snake[0].y) return true
            }
            const LeftWallCollision = snake[0].x < 0;
            const RightWallCollision = snake[0].x > gameCanvas.width - 30;
            const TopWallCollision = snake[0].y < 0;
            const BottomWallCollision = snake[0].y > gameCanvas.height - 30;
            return LeftWallCollision || RightWallCollision || TopWallCollision || BottomWallCollision
        }

        /**
         * –ì–µ–Ω–µ—Ä–∏—Ä—É–µ—Ç —Ä–∞–Ω–¥–æ–º–Ω–æ–µ —á–∏—Å–ª–æ —É–º–Ω–æ–∂–µ–Ω–Ω–æ–µ –Ω–∞ 10 –æ—Å–Ω–æ–≤—ã–≤–∞—è—Å—å –Ω–∞ –º–∏–Ω–∏–º–∞–ª—å–Ω–æ–º –∏ –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–º —á–∏—Å–ª–µ
         **/
        function randomNumber(min: number, max: number) {
            return Math.round((Math.random() * (max - min) + min) / 30) * 30;
        }

        /**
         * –°–æ–∑–¥–∞–µ—Ç –µ–¥—É –≤ —Ä–∞–Ω–¥–æ–º–Ω—ã—Ö –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞—Ö
         */
        function spawnFood() {
            foodX = randomNumber(0, gameCanvas.width - 30);
            foodY = randomNumber(0, gameCanvas.height - 30);
            // –ï—Å–ª–∏ –µ–¥–∞ —Å–æ–∑–¥–∞–µ—Ç—Å—è —Ç–∞–º, –≥–¥–µ –∑–º–µ–π–∫–∞ –Ω–∞—Ö–æ–¥–∏—Ç—Å—è —Å–µ–π—á–∞—Å, —Ç–æ –≥–µ–Ω–µ—Ä–∏—Ä—É–µ—Ç –µ–¥—É –≤ –Ω–æ–≤–æ–π –ª–æ–∫–∞—Ü–∏–∏
            snake.forEach(function foodSnakeCheck(part) {
                const foodRecreate = part.x === foodX && part.y === foodY;
                if (foodRecreate) spawnFood();
            });
        }

        /**
         * –†–∏—Å—É–µ—Ç –≥–æ–ª–æ–≤—É –∑–º–µ–π–∫–∏
         */
        function drawSnake() {
            snake.forEach(drawSnakePart)
        }

        /**
         * –†–∏—Å—É–µ—Ç —á–∞—Å—Ç–∏ –∑–º–µ–π–∫–∏
         */
        function drawSnakePart(snakePart: { x: number; y: number }) {
            ctx.fillStyle = SnakeColor;
            ctx.strokeStyle = SnakeBorderColor;
            // –†–∏—Å—É–µ—Ç —Å–∞–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫ –¥–ª—è –∑–º–µ–π–∫–∏
            ctx.fillRect(snakePart.x, snakePart.y, 30, 30);
            // –†–∏—Å—É–µ—Ç –±–æ—Ä–¥–µ—Ä –¥–ª—è –∑–º–µ–π–∫–∏
            ctx.strokeRect(snakePart.x, snakePart.y, 30, 30);
        }

        /**
         * –ò–∑–º–µ–Ω—è–µ—Ç –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω—É—é –∏ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—É—é —Å–∫–æ—Ä–æ—Å—Ç—å –¥–ª—è –∑–º–µ–π–∫–∏ –æ—Å–Ω–æ–≤—ã–≤–∞—è—Å—å –Ω–∞ –Ω–∞–∂–∞—Ç–æ–π –∫–Ω–æ–ø–∫–µ
         */
        function changeDirection(event: { keyCode: any; }) {
            const leftArrowKey = 37;
            const rightArrowKey = 39;
            const upArrowKey = 38;
            const downArrowKey = 40;

            /**
             –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ—Ç –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–∫–æ—Ä–æ—Å—Ç–∏ –Ω–∞ –ø—Ä–æ—Ç–∏–≤–æ–ø–æ–ª–æ–∂–Ω—É—é
             */
            if (changingDirection) return;
            changingDirection = true;

            const pressedKey = event.keyCode;
            const movingUp = dy === -30;
            const movingDown = dy === 30;
            const movingRight = dx === 30;
            const movingLeft = dx === -30;

            if (pressedKey === leftArrowKey && !movingRight) {
                dx = -30;
                dy = 0;
            }
            if (pressedKey === upArrowKey && !movingDown) {
                dx = 0;
                dy = -30;
            }
            if (pressedKey === rightArrowKey && !movingLeft) {
                dx = 30;
                dy = 0;
            }
            if (pressedKey === downArrowKey && !movingUp) {
                dx = 0;
                dy = 30;
            }
        }
    }

    render() {
        return <div/>;
    }
}
